.filteredUi {
  min-height: 120px;
  height: fit-content;
  display: flex;
  align-items: center;
  padding: 0 1rem;
  margin: 0 auto;
  gap: 20px;
  background-color: #ffffff;
  border-bottom: 0.3px solid rgb(144 144 144 / 12%);
  width: 95%;
  position: relative;
}
.filteredUiBox {
  position: relative;
}
.openFilterBox {
  position: absolute;
  right: 2rem;

  z-index: 333;
  height: 40px;
  background-color: #a13de9;
  color: white;
  font-size: 16px;
  letter-spacing: 1.1px;
  background: var(--gradient_bg);
  border-radius: 3px;
  bottom: -3rem;
}

.filteredUiLeft {
  display: flex;
}
.filteredUi-left-container {
  flex: 6;
  cursor: pointer;
}
.filteredUiRight {
  display: flex;
  justify-content: space-evenly;
  gap: 10px;
  flex: 6;
}
.chipsContainer {
  padding: 0;
}
.filterText {
  color: gray;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: 1.1px;
  font-family: var(--poppins);
}
.filterItem {
  display: flex;
  min-width: 70px;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 5px;
}
.resultFoundText {
  color: gray;
  font-family: var(--poppins);
  margin: 8px 0;
  font-size: 13px;
}
.filteredUiBox {
}
.filteredUiBox select {
  background-color: transparent !important;
}
.search_results {
  margin: 25px 0;
  margin-bottom: 5px;
}
.labelName {
  color: rgb(167, 167, 167);
  margin-bottom: 3px;
  display: block;
  font-weight: 100;
  letter-spacing: 1.1px;
  font-size: 12px;
  font-family: var(--poppins);
  padding-left: 6px;
  color: rgba(137, 43, 226, 0.582);
}
.filter_item_list {
  height: 45px;
  text-align: start;
  padding: 5px 10px;
  border-radius: 3px;
  background-color: #ebebeb59;

  box-shadow: rgba(139, 138, 138, 0.2) 0px 2px 8px 0px;
  display: flex;
  align-items: center;
}
.filter_item_list * {
  font-weight: 100;
  letter-spacing: 1.1px;
  color: #413f44;
  font-size: 13px;
}
.techFilterSelect {
  width: 100%;
  border: none;
  outline: none;
  border-radius: 9px;
}
.labelName {
  font-size: 10px;
  margin-bottom: 5px;
  white-space: nowrap;
  font-family: "poppins";
  text-align: start;
  color: #939393;
}
.filterRightItemWrapper {
  flex: 1;
}

select {
  appearance: none !important;
  padding: 0 10px !important;
  font-family: "poppins" !important;
}
.filterRowIcon {
  font-size: 1.82rem !important;
  fill: blueviolet;
}

@media screen and (max-width: 768px) {
  .openFilterBox {
    position: absolute;
    right: 2rem;
    z-index: 333;
    height: 33px;
    background-color: #a13de9;
    color: white;
    font-size: 11px;
    letter-spacing: 1.1px;
    background: var(--gradient_bg);
    border-radius: 3px;
    bottom: -3rem;
    left: 2rem;
    top: -1.1rem;
  }
}
