.navbar {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  padding: 0 1rem;
  background-color: transparent;
  transition: all 0.3s ease-in-out;
  z-index: 998;
  height: 70px;
}
.blurNav {
  backdrop-filter: blur(40px);
  background-color: rgba(255, 255, 255, 0.6);
}
.nav_hide * {
  /* color: white; */
}
.nav_hide .nav_list_item {
  /* color: white; */
}
.nav_left {
  flex: 1;
  padding-left: 2rem;
  display: flex;
  justify-content: start;
}

.nav_left img {
  cursor: pointer;
}

.nav_list {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 1rem;
}
.nav_list_item {
  color: rgb(92 92 92);
  letter-spacing: 1.2px;
  font-size: 11px;
  font-weight: 500;
  cursor: pointer;
  font-family: "poppins";
}

.activeNav {
  position: relative;
  display: inline-block;
  transition: all 0.3s ease;
}

.activeNav::after {
  content: "";
  width: 80%;
  height: 3px;
  border-radius: 20px;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  background: var(--gradient_bg);
  bottom: -1rem;
}
.navDropDown {
  z-index: 995;
  display: none;
}
.dropDownIcon {
  display: none;
}
.navSignUpButton {
  background: var(--gradient_bg);
  border-radius: 3px;
  margin-left: 2rem;
  color: white !important;
  height: 35px;
  padding: 0 1rem;
  display: flex;
  align-items: center;
  font-size: 12px !important;
  font-family: "poppins";
  letter-spacing: 1.1px;
  gap: 10px;
}

/* Extra small devices (phones, 600px and down) */
@media screen and (max-width: 600px) {
  .dropDownIcon {
    display: block;
  }
  .navDropDown {
    display: block;
  }
  .nav_right {
    display: none;
  }

  .navDropDown {
    position: absolute;
    backdrop-filter: blur(40px);
    background-color: rgb(255, 255, 255);
    border-radius: 8px;
    top: 0;
    height: 0;
    overflow: hidden;
    left: 0;
    transition: all 0.3s ease;
    width: 100%;
  }
  .nav_list_dropdown {
    display: flex;

    height: fit-content;
    padding: 2rem 0;
    flex-direction: column;
    justify-content: flex-start;
  }
  .openDropDown {
    height: auto;
    overflow: visible;
  }
  .navDropDown .navbar {
    overflow: initial;
  }
  .nav_list_dropdown .navLink {
    display: flex;
    align-items: center;
    align-items: center;
    padding: 0 1rem;
    height: 50px;
  }
  .nav_list_item {
    font-size: 13px;
    padding: 0 20px;
  }
  .navDropDown .navLink {
    transition: all 0.7s ease-in;
    border-bottom: 0.3px solid gainsboro !important;
  }
  .activeNav {
    background: var(--gradient_bg_light);
  }
  .navLink:hover {
    background: linear-gradient(
      45deg,
      rgba(90, 19, 196, 0.349) 16%,
      rgba(139, 6, 227, 0.377) 48%,
      rgba(11, 117, 255, 0.192) 100%
    );
  }
  .navLink:hover .nav_list_item {
    color: white;
  }
  .navLink:hover .navDropDownIcon {
    fill: white;
  }
  .activeNav .nav_list_item {
    color: white;
  }
  .activeNav .navDropDownIcon {
    fill: white;
  }
  .navDropDown .activeNav::after {
    width: 0;

    bottom: 0;
    margin: 0;
  }
  .navDropDownIcon {
    fill: #745590;
  }
  .closeDropDown {
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    padding-bottom: 1rem;
    cursor: pointer;
  }
  .closeDropDownIcon {
    font-size: 1.5rem;
    fill: #745574;
  }
  .closeDropDown li {
    text-transform: uppercase;
    color: rgba(53, 8, 66, 0.849);
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media screen and (min-width: 1024px) {
  .nav_list_item {
    font-size: 13px;
  }
}
