.filterSidebar {
  width: 300px;
  height: 100vh;
  background-color: rgb(255, 255, 255);
  padding: 1rem;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  position: fixed;
  left: 0;
  top: 0;
  transition: all 0.4s ease-in-out;
  z-index: 1000;
}
.hide {
  transform: translateX(-300px);
}
.closeSidebar {
  background: var(--gradient_bg);
  color: white;
  font-size: 16px;
  border-radius: 3px;
  height: 45px;
  margin-left: auto;
}
