.js {
  top: 8rem;
  left: 15rem;
  width: 70px !important;
}
.py {
  top: 12rem !important;
  right: 18rem !important;

  width: 50px !important;
}
.java {
  top: 8rem;
  right: 8rem;
  width: 90px !important;
}
.img_animate {
  position: absolute;
  z-index: 9999;
  opacity: 0.3;
  transition: all 0.3s ease-in;
  animation: animateImg 14s linear infinite;
}

.img1 {
  filter: drop-shadow(9px 1px 3px #bcdd44ad);
}

@keyframes animateImg {
  0% {
    transform: rotate(4deg) translateX(-10px) translateY(10px);
  }
  15% {
    transform: rotate(6deg) translateX(-20px) translateY(30px);
  }

  30% {
    transform: rotate(7deg) translateX(-30px) translateY(50px);
  }
  45% {
    transform: rotate(8deg) translateX(-40px) translateY(40px);
  }

  60% {
    transform: rotate(4deg) translateX(-50px) translateY(50px);
  }

  75% {
    transform: rotate(4deg) translateX(-40px) translateY(40px);
  }

  90% {
    transform: rotate(4deg) translateX(-30px) translateY(30px);
  }

  90% {
    transform: rotate(4deg) translateX(-20px) translateY(20px);
  }
  100% {
    transform: rotate(4deg) translateX(-0px) translateY(10px);
  }
}
@media screen and (max-width: 600px) {
  .js {
    top: 14rem;
    left: 0rem;
    width: 70px !important;
  }
  .java {
    right: 5rem;
  }
  .py {
    right: 0rem;
  }
  .active_tool::after {
    width: 30px !important;
  }
  /* p .active_tool::after {
    width: 30px !important;
  } */
  .link_item {
    height: 45px;
  }
  .technologies_tab_item {
    font-size: 9px;
  }
  .technologies_tab_item {
    padding: 9px 8px;
  }
  .technologies_tab_list {
    gap: 10px;
  }
  .technologies_list {
    width: 100%;
    gap: 16px;
  }
  .technology_item {
    width: 45px;
    height: 45px;
    padding: 0px;
  }
}
@media screen and (min-width: 600px) and (max-width: 768px) {
  p .active_tool::after {
    width: 40px !important  ;
  }
  .link_item {
    height: 45px;
  }
  .technologies_tab_item {
    font-size: 10px;
  }
  .technologies_tab_item {
    padding: 7px 10px;
  }
  .technologies_tab_list {
    gap: 10px;
  }
  .technologies_list {
    width: 100%;
    gap: 16px;
  }
  .technology_item {
    width: 45px;
    height: 45px;
    padding: 0px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .link_item {
    height: 50px;
  }
  .technology_item {
    width: 50px;
    height: 50px;
    padding: 0px;
  }
  .technologies_list {
    width: 72%;
    gap: 10px 15px;
  }
  .technologies_innerbox {
    width: 100%;
    margin: 0 auto;
    padding: 0rem 0;
  }
  .technologies_tab_item {
    font-size: 12px;
  }
}
