.banner {
  height: 100vh;
  display: flex;
  padding-top: 70px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  scroll-snap-align: start;
}
.bannerAnimationBox {
  position: relative;
}
.bannerAnimationBox::before {
  width: 100%;
  content: "";
  z-index: 99;
  position: absolute;
  right: 0;
  top: 0;
  height: 100vh;
  background: linear-gradient(
    90deg,
    rgb(255 255 255 / 80%) 23%,
    rgb(255 240 240 / 0%) 34%,
    rgb(208 208 208 / 46%) 43%,
    rgb(122 24 240 / 20%) 100%
  );
}
.banner_user_profile {
  user-select: none;
  padding-left: 2rem;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  position: relative;
  padding-top: 1.2rem;
}
.banner_user_info {
  width: 80%;
}
.banner_user_imageBox {
  text-align: start;
}

.banner_user_info_header_text {
  margin: 8px 0;
  color: rgb(71, 71, 71);
  letter-spacing: 1.1px;
}
.banner_user_info p {
  font-size: 13px;
  line-height: 37px;
  text-align: start;
  color: #5e5e5e;
}
.banner_user_imageBox img {
  filter: drop-shadow(7px 10px 12px rgba(0, 0, 0, 0.3));
  border-radius: 50%;
  width: 280px;
  height: 280px;
  object-fit: cover;
}
.banner_animations {
  flex: 7;
}
.banner_user_info p {
  color: #474646ad;
  line-height: 30px;

  font-family: "Poppins";
  font-size: 18px;
}
.banner_user_info_main {
  color: #4b00d1cf;
  text-align: start;
  letter-spacing: 1.1px;
  margin: 1rem;
  position: relative;
  line-height: 50px;
  font-size: 2rem;
}
