.banner_user_info_desc {
  display: flex;
  flex-direction: column;
  margin-top: 1.5rem;
  padding-right: 1.2rem;
}
.banner_container {
  position: relative;
  display: flex;
  min-height: 100vh;
}
.social_media_icons {
  position: absolute;
  bottom: 10px;
  z-index: 101;
  left: 20px;
  display: flex;
  gap: 20px;
}
.socialMedia_icon {
  cursor: pointer;
  transition: transform 0.5s ease-in-out;
  width: 40px;
}
.socialMedia_icon:hover {
  transform: scale(1.5) rotate(360deg);
}

.cv_button {
  width: 380px !important;
  height: 60px !important;
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-top: 2rem;
  background: var(--gradient_bg);
  color: white;
  padding: 8px 23px;
  letter-spacing: 1.1px;
  text-transform: uppercase;
  border-radius: 3px;
}
.cv_button span {
  font-size: 24px;
  font-weight: 500 !important;
  font-family: "Poppins";
}
.brainy {
  font-weight: bold;
  font-size: 2rem;
  font-family: "PT Serif", serif !important;
}
.fullStack {
  position: relative;
  color: black;
  display: inline-block;
  font-weight: 400;
}
.developerName {
  font-weight: 300;
  font-size: 12px;
  margin: 0;
  position: absolute;
  bottom: -2.5rem;
  font-family: "Poppins";
  color: rgb(39, 39, 39);
}
.fullStack::after {
  content: "";
  width: 89px;
  height: 2.4px;
  position: absolute;
  background-color: #5414cba6;
  bottom: 7px;
  right: -106px;
}
.downloadIcon {
  width: 2.6rem !important;

  height: 4rem !important;
  color: rgb(226, 226, 226) !important;
}
.headerProfileTop {
  display: flex;
  align-items: center;
}
.hireMeButton img {
  width: 45px !important;
}

.hireMeButton {
  border: 0.3px solid gainsboro;
  width: 180px;
  align-items: center;
  padding: 5px 15px;
  color: gray;
  border-radius: 37px;
  letter-spacing: 1.1px;
  font-family: "Kalam", cursive;
  border-color: #892be27a;
  height: 49px;
  cursor: pointer;
  font-size: 15px;
  background-color: rgba(239, 240, 241, 0.712);
  color: #8a2be2bd;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}
.buttonBoxBanner {
  margin-top: 3rem;
  width: 100%;
  display: flex;
  gap: 2rem;

  align-items: center;
}
.cvButtonBanner {
  font-family: "Kalam", cursive;
}
.cvButtonBanner {
  width: 230px !important;
  height: 49px !important;
  position: relative;
  z-index: 99999;
  display: flex;
  align-items: center;
  gap: 1rem;
  background: var(--gradient_bg);
  color: white;
  padding: 8px 23px;
  letter-spacing: 1.1px;
  text-transform: uppercase;
  font-size: 15px;
  border-radius: 37px;
}
@media screen and (max-width: 768px) {
  .banner_user_info_main {
    width: 7 0% !important;
  }
  .bannerAnimationBox {
    opacity: 0.5 !important ;
  }
  .banner {
    position: absolute !important;
    width: 100vw;
    z-index: 99;
  }
  .banner_user_profile {
    padding-left: 0;
    gap: 3rem;
  }
  .banner_user_imageBox img {
    filter: drop-shadow(7px 10px 12px rgba(0, 0, 0, 0.3));
    border-radius: 50%;
    width: 124px;
    height: 124px;
    border: 6px solid white;
  }
  .headerProfileTop {
    /* flex-direction: column; */
    justify-content: center;
  }
  .banner_user_info p {
    line-height: 30px;
    font-size: 13px;
    color: rgb(2, 2, 34);
    text-align: center;
  }
  .banner_user_info {
    padding: 0 1rem;
    margin: 0 auto;
    width: 90% !important;
  }
  .buttonBoxBanner {
    justify-content: center;
  }
  .fullStack {
    font-size: 1.2rem;
    padding-left: 3rem;
  }
  .developerName {
    padding-left: 6rem;
  }
  .banner_user_info_main {
    line-height: 50px;
  }
  .brainy {
    font-weight: bold;
    font-size: 3rem;
    font-family: "PT Serif", serif !important;
  }
  .cv_button {
    width: 240px !important;
    height: 50px !important;
    padding: 8px;
    letter-spacing: 1.1px;
    text-transform: uppercase;
    border-radius: 3px;
  }
  .cv_button span {
    font-size: 18px;
    font-weight: 500 !important;
    font-family: "Poppins";
  }
  .socialMedia_icon {
    width: 30px;
  }
  .hireMeButton {
    width: 150px !important;
  }
  .hireMeButton img {
    width: 30px !important;
  }
  .cvButtonBanner {
    padding: 8px 13px;
    font-size: 12px;
    gap: 0 !important;
  }
  .cvButtonBanner img {
    width: 30px !important;
  }
}

