.project_container {
  padding-left: 2rem;
  align-items: center;
  display: grid;
  position: relative;
  grid-template-columns: 1fr 1fr 1fr;
}

.projects {
  background: none;
  display: flex;
  margin: auto;
  width: 800px;
}
.theprojectSection {
  padding-bottom: 4rem;
  height: 3 00px;
  width: 100%;
}
.next_project_btn {
  background: rgb(111, 30, 233);
  background: linear-gradient(
    45deg,
    rgba(111, 30, 233, 0.7321814254859611) 16%,
    rgba(139, 6, 227, 0.7926565874730022) 48%,
    rgba(11, 115, 255, 0.7710583153347732) 100%
  );
}
.projects_header {
  margin-bottom: 2rem;
  text-align: center;
}
.projects_header h1 {
  letter-spacing: 1.1px;
  width: max-content;
  color: #5e7cd4;
  font-family: "Space Grotesk", sans-serif;
}
.project_title::after {
  content: "";
  border-radius: 38px 100px;
  position: absolute;
  bottom: -1rem;
  height: 8px;
  width: 33%;
  background: linear-gradient(90deg, #5e7bd4b0 31%, #df00dfaf 73%);
}
.projects_header h1 span {
  color: #df00df;
  font-family: "Space Grotesk", sans-serif;
  margin-right: 8px;
}
.projects_header p {
  color: rgb(88, 88, 88);
  font-weight: 500;
  font-size: 21px;
  line-height: 32px;
  display: inline;
  font-family: "Space Grotesk", sans-serif;
}
.project_title {
  margin: 0 auto;
  gap: 1.5rem;
  margin-bottom: 3rem;
  justify-content: center;
  align-items: center;
  position: relative;
  display: inline-flex;
}
#projects {
  width: 650px;
}

@media screen and (max-width: 768px) {
  .projects_header p {
    width: 100% !important;
    font-size: 12px;
    font-family: "Space Grotesk", sans-serif;
  }
  #projects {
    width: 100%;
  }
}
