.theProject {
  width: 90%;
  margin: auto;
}
.theProjectContainer {
  display: flex;
  flex-wrap: wrap;
}
.projectsHeader {
}
.viewMoreBox {
  text-align: end;
}
.theProjectSectionHeader {
  display: flex;
  align-items: center;
  margin: 2rem auto !important;
  gap: 2rem;
}
.projectHeaderText {
  font-weight: 400;
  border-radius: 3px;
  margin: 0 !important;
  background: #ebebeb59;
  box-shadow: #9c47eb40 -1px 3px 5px 2px;
  height: 45px;
}
.projectHeaderText h3 {
  font-weight: 400;
  color: #9d47eb;
  font-size: 1rem;
  font-family: "poppins";
  letter-spacing: 1.1px;
}
.theProjectSectionHeader button {
  width: fit-content;
  border-radius: 3px;
  box-shadow: #9c47eb40 -1px 3px 5px 2px;
  padding: 0 1rem;
  background-color: #ebebeb59;
  color: white;
  height: 45px;
  background-color: rgb(139 138 138 / 20%) 0px 2px 8px 0px;
  border-radius: 3px;
  font-family: "poppins";
  font-weight: 400;
  font-size: 1rem;
  color: #9d47eb;
  letter-spacing: 1.1px;
}
.viewMoreProjectPageBtn {
  width: 30px;
}
@media screen and (min-width: 1200px) {
  .theProjectContainer {
    display: flex;
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 768px) {
  .theProjectSectionHeader button {
  }
  .theProjectSectionHeader img {
    width: 27px;
  }
  .theProjectContainer {
    align-items: center;
    justify-content: center;
  }
}
@media screen and (max-width: 480px) {
}
